
import HomePage from '../pages/home.f7.html';
import AnimelistPage from '../pages/animelist.f7.html';
import FormPage from '../pages/form.f7.html';
import SearchPage from '../pages/search.f7.html';
import MyAnime from '../pages/myanime.f7.html';
import TypeAndGenre from '../pages/genre.f7.html';
import Community from '../pages/communety.f7.html';
import News from '../pages/news.f7.html';
import Chat from '../pages/chat.f7.html';

import Genere from '../pages/generes.f7.html';

import LeftPage1 from '../pages/left-page-1.f7.html';
import LeftPage2 from '../pages/left-page-2.f7.html';
import DynamicRoutePage from '../pages/dynamic-route.f7.html';
import RequestAndLoad from '../pages/request-and-load.f7.html';
import NotFoundPage from '../pages/404.f7.html';

var routes = [
  {
    path: '/',
    component: HomePage,
    options: {
      transition: 'f7-dive',
    },
  },
  {
    path: '/animelist/',
    component: AnimelistPage,
    options: {
      transition: 'f7-dive',
    },
  },
  {
    path: '/search/',
    component: SearchPage,
    options: {
      transition: 'f7-dive',
    },
  },
  {
    path: '/form/',
    component: SearchPage,
    options: {
      transition: 'f7-dive',
    },
  },
  {
    path: '/myanime/',
    component: MyAnime,
    options: {
      transition: 'f7-dive',
    },
  },
  {
    path: '/genre/',
    component: TypeAndGenre,
    options: {
      transition: 'f7-dive',
    },
  },
  {
    path: '/communety/',
    component: Community,
    options: {
      transition: 'f7-dive',
    },
  },
  {
    path: '/chat/',
    component: Chat,
    options: {
      transition: 'f7-dive',
    },
  },
  {
    path: '/news/',
    component: News,
    options: {
      transition: 'f7-dive',
    },
  },
  {
    path: '/left-page-1/',
    component: LeftPage1,
  },
  {
    path: '/left-page-2/',
    component: LeftPage2,
  },
  {
    path: '/dynamic-route/blog/:blogId/post/:postId/',
    component: DynamicRoutePage,
    options: {
      transition: 'f7-dive',
    },
  },
  {
    path: '/showinfo/:userId/',
    options: {
      transition: 'f7-dive',
    },
    async: function (routeTo, routeFrom, resolve, reject) {
      // Router instance
      var router = this;

      // App instance
      var app = router.app;

      // Show Preloader
      app.preloader.show();

      // User ID from request
      var userId = routeTo.params.userId;
      console.log(userId);
      app.request.promise.get('https://snoanime.com/web/info.php?id='+userId)
      .then(function (res) {
        console.log(app.methods.parseData(res.data)); 
        var datas = app.methods.parseData(res.data);
        datas.link = {
          links: [
            {
              title: 'Framework7 Website',
              url: 'http://framework7.io',
            },
            {
              title: 'Framework7 Forum',
              url: 'http://forum.framework7.io',
            },
          ]
        }
      // Hide Preloader
      app.preloader.hide();
      // Resolve route to load page
      resolve(
        {
          component: RequestAndLoad,
        },
        {
          context: {
            user: datas,
          }
        }
      );

      })
      .catch(function (err) {
        app.preloader.hide();
        app.dialog.alert("توجد مشكلة في الشبكة حاول فتح الانمي مرى أخرى");
        console.log(err.xhr)
        console.log(err.status)
        console.log(err.message)
      })
    },
  },
  {
    path: '/opengenere/:userId/',
    options: {
      transition: 'f7-dive',
    },
    async: function (routeTo, routeFrom, resolve, reject) {
      // Router instance
      var router = this;

      // App instance
      var app = router.app;

      // Show Preloader
      app.preloader.show();

      // User ID from request
      var userId = routeTo.params.userId;
      var userids = localStorage.getItem("opengenere");
      localStorage.removeItem("opengenere");
      app.request.promise.get('https://snoanime.com/'+userids)
      .then(function (res) {
        console.log(btoa(res.data)); 
        var datas = btoa(res.data);
        app.preloader.hide();
        resolve(
         {
            component: Genere,
          },
          {
          context: {
            user: datas,
          }
         }
         );

         })
        .catch(function (err) {
         app.preloader.hide();
         app.dialog.alert("توجد مشكلة في الشبكة حاول فتح الانمي مرى أخرى");
         console.log(err.xhr)
         console.log(err.status)
         console.log(err.message)
        })
      },
    },
    {
    path: '(.*)',
    component: NotFoundPage,
  },
];

export default routes;